import React from "react";
import "../css/result.css";
import {ResultDB} from "../component/ResultDB";

class result extends React.Component{
    state = {
        value : 0,
        array : {} 
    }
    render(){
        const{location} = this.props;
        for(let i = 0;i< ResultDB.length;i++){
            if(location.state.mbti === ResultDB[i].id){
                this.state.value = i;
                break;
            } 
        }
        return (
            <div>
                <div className = "result_title">
                    <div>당신의 종목은...</div>
                    {ResultDB[this.state.value].stock}
                </div>
                <div className = "result_explain">
                    <ul className = "result_list"><li>성격은?</li></ul>
                    {ResultDB[this.state.value].match}
                </div>
                <div className = "result_explain">
                    {ResultDB[this.state.value].personal}
                </div>
            </div>
        )
    }
}


export default result;